.title-container {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 1rem;
}

.title {
    font-size: 23px;
    font-weight: 700;
    color: #152238;
}

.subtitle {
    font-size: 14px;
    font-weight: 400;
    color: #fff;
}

.daily-item {
  background-color: #f5f5f5;
  border-radius: 15px;
  height: 40px;
  margin: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  padding: 5px 20px;
}

.icon-small {
  width: 40px;
}

.day {
  color: #212121;
  flex: 1 1;
  font-weight: 600;
  margin-left: 15px;
}

.description {
  flex: 1 1;
  margin-right: 15px;
  text-align: right;
  color: #152238;
}

.min-max {
  color: #757575;
}

.daily-details-grid {
    grid-row-gap: 0;
    grid-column-gap: 15px;     
    row-gap: 0;
    column-gap: 15px;
    display: grid;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
}

.daily-details-grid-item {
    display: flex;
    height: 30px;
    justify-content: space-between;
    align-items: center;
}

.daily-details-grid-item label:first-child {
    color: #152238;
    font-weight: 600;
}

.daily-details-grid-item label:last-child {
    color: #fff
}
